// Подключение своих шрифтов.
// Не пишите тут ничего, кроме @font-face

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.ttf") format("truetype");
  font-display: swap;
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Montserrat-ExtraBold.ttf") format("truetype");
  font-display: swap;
}
