@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.ttf") format("truetype");
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Montserrat-ExtraBold.ttf") format("truetype");
  font-display: swap; }

.chrome {
  background-image: url(../img/sprite-07819257116036062.png);
  background-position: 0px 0px;
  width: 64px;
  height: 64px; }

.github-logo {
  background-image: url(../img/sprite-07819257116036062.png);
  background-position: -68px 0px;
  width: 64px;
  height: 64px; }

.placeholder {
  background-image: url(../img/sprite-07819257116036062.png);
  background-position: 0px -68px;
  width: 64px;
  height: 64px; }

@-ms-viewport {
  width: device-width; }

.page {
  box-sizing: border-box;
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -ms-overflow-style: scrollbar;
  -ms-text-size-adjust: 100%;
  min-width: 320px;
  min-height: 100%; }
  .page__inner::after {
    content: "";
    position: absolute;
    background: url("../img/circle-top.png") no-repeat;
    top: 0;
    left: 0;
    width: 21.5%;
    height: 415px;
    z-index: -10; }
  .page__content {
    margin-left: auto;
    margin-right: auto;
    padding-left: 7.5%;
    padding-right: 7.5%;
    padding-top: 10vh;
    padding-bottom: 10vh; }
  .page__footer-wrapper {
    flex-shrink: 0; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background-color: #fff;
  margin: 0;
  min-height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, Montserrat, Helvetica Neue, sans-serif;
  font-size: 14px;
  line-height: 1.4;
  color: #231F20; }

[tabindex='-1']:focus {
  outline: none !important; }

[role='button'] {
  cursor: pointer; }

input[type='search'] {
  -webkit-appearance: textfield; }

a,
button,
[role='button'],
input,
label,
select,
textarea {
  touch-action: manipulation; }

a svg,
button svg {
  pointer-events: none; }

img {
  max-width: 100%;
  height: auto; }

.main-nav {
  width: 50vh;
  height: 50vh;
  border-radius: 50%;
  overflow: hidden; }
  .main-nav__list {
    position: relative;
    width: 50vh;
    height: 50vh;
    padding: 0;
    margin: 0 auto;
    list-style: none;
    border: 9px solid #f1f1f1;
    border-radius: 50%;
    transform: rotate(-45deg); }
    .main-nav__list::after {
      content: "";
      border-radius: 50%;
      width: 14vh;
      height: 14vh;
      box-sizing: border-box;
      background-color: #fff;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      z-index: 15; }
  .main-nav__item {
    position: absolute;
    width: 25vh;
    height: 25vh;
    left: -10px;
    top: -10px;
    overflow: hidden;
    transform-origin: 100% 100%;
    transition: .5s; }
    .main-nav__item:hover, .main-nav__item:focus {
      background-color: #f1f1f1; }
    .main-nav__item:nth-child(1) {
      transform: rotate(0deg); }
    .main-nav__item:nth-child(2) {
      transform: rotate(90deg); }
    .main-nav__item:nth-child(3) {
      transform: rotate(180deg); }
    .main-nav__item:nth-child(4) {
      transform: rotate(270deg); }
    .main-nav__item--active {
      background-color: #f1f1f1; }
      .main-nav__item--active .main-nav__link span {
        opacity: 1; }
      .main-nav__item--active .main-nav__link svg {
        fill: #d260f7; }
  .main-nav__link {
    display: block;
    width: 25vh;
    height: 25vh;
    text-decoration: none; }
    .main-nav__link:hover span,
    .main-nav__link:focus span {
      opacity: 1; }
    .main-nav__link:hover svg,
    .main-nav__link:focus svg {
      transition: .5s; }
    .main-nav__link--about {
      transform: rotate(45deg);
      padding-top: 63%;
      padding-left: 48%; }
      .main-nav__link--about svg {
        left: 65%; }
      .main-nav__link--about:hover svg,
      .main-nav__link--about:focus svg {
        fill: #d260f7; }
    .main-nav__link--experience {
      transform: rotate(-45deg);
      padding-top: 71%;
      padding-left: 39%; }
      .main-nav__link--experience svg {
        top: 58%; }
      .main-nav__link--experience:hover svg,
      .main-nav__link--experience:focus svg {
        fill: #5699fe; }
    .main-nav__link--portfolio {
      transform: rotate(-135deg);
      padding-top: 62%;
      padding-left: 19%; }
      .main-nav__link--portfolio svg {
        left: 40%; }
      .main-nav__link--portfolio:hover svg,
      .main-nav__link--portfolio:focus svg {
        fill: #f56a60; }
    .main-nav__link--skills {
      transform: rotate(135deg);
      padding-top: 48%;
      padding-left: 39%; }
      .main-nav__link--skills svg {
        top: 35%; }
      .main-nav__link--skills:hover svg,
      .main-nav__link--skills:focus svg {
        fill: #00dbaf; }
    .main-nav__link svg {
      fill: #9b9b9b; }
    .main-nav__link span {
      display: block;
      opacity: 0;
      font-size: 1.8vh;
      color: #656565; }
  .main-nav__item-icon {
    position: absolute;
    display: block;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 5.5vh;
    height: 5.5vh; }

.side-bar {
  width: 35.5%; }
  .side-bar__title {
    margin: 0;
